export const calculateDailyProtein = (idealWeight: string, age: string): string => {
  const iw = parseFloat(idealWeight);
  const parsedAge = parseInt(age, 10);
  if (!iw || isNaN(parsedAge)) return '';

  const multiplier = parsedAge >= 65 ? 1.1 : 1.4;
  const protein = iw * multiplier;
  return protein.toFixed(2);
};
