import React, { useRef, useState, useEffect } from 'react';
import styled from 'styled-components';
import { ComputedValues } from './UserInputForm';
import PrintIcon from '@mui/icons-material/Print';
import { useReactToPrint } from 'react-to-print';
import BloodPressureChart from './utils/BloodPressureChart';
import { ResponsivePie } from '@nivo/pie';

interface Props {
  computed: ComputedValues;
}

const DAILY_KCAL_PER_KG = 250;
const NUTRIENT_KCAL_PER_GRAM = { carb: 4, protein: 4, fat: 9 };
const RATIOS = {
  normal: { label: ' ', carb: 4, protein: 3, fat: 3 },
  lowCarb: { label: '', carb: 2, protein: 4, fat: 4 },
  highCarb: { label: '', carb: 5, protein: 3, fat: 2 },
};

function computeMacros(totalKcal: number, ratioType: keyof typeof RATIOS) {
  const ratio = RATIOS[ratioType];
  const total = ratio.carb + ratio.protein + ratio.fat;
  return {
    label: ratio.label,
    carb: ((totalKcal * ratio.carb) / total) / NUTRIENT_KCAL_PER_GRAM.carb,
    protein: ((totalKcal * ratio.protein) / total) / NUTRIENT_KCAL_PER_GRAM.protein,
    fat: ((totalKcal * ratio.fat) / total) / NUTRIENT_KCAL_PER_GRAM.fat,
  };
}

