const ReportLayout: React.FC<Props> = ({ computed }) => {
  const componentRef = useRef<HTMLDivElement>(null);
  const handlePrint = useReactToPrint({ contentRef: componentRef, documentTitle: 'Report' });

  const [goal, setGoal] = useState('');
  const [targetWeight, setTargetWeight] = useState('0');

  

  const parsedTargetWeight = parseFloat(targetWeight || '0');
  let goalKcal = 0;
  if (goal === '') goalKcal = -parsedTargetWeight * DAILY_KCAL_PER_KG;
  else if (goal === '') goalKcal = parsedTargetWeight * DAILY_KCAL_PER_KG;
  const bmrValue = parseFloat(computed.bmr || '0');
  const metabolicKcal = bmrValue * 1.55 + goalKcal;

  const [macros, setMacros] = useState({
    normal: computeMacros(metabolicKcal, 'normal'),
    lowCarb: computeMacros(metabolicKcal, 'lowCarb'),
    highCarb: computeMacros(metabolicKcal, 'highCarb'),
  });

  useEffect(() => {
    // Step 1:   0 
    setMacros({
      normal: { label: ' ', carb: 0, protein: 0, fat: 0 },
      lowCarb: { label: '', carb: 0, protein: 0, fat: 0 },
      highCarb: { label: '', carb: 0, protein: 0, fat: 0 },
    });
  
    // Step 2:     
    const timeout = setTimeout(() => {
      const updated = {
        normal: computeMacros(metabolicKcal, 'normal'),
        lowCarb: computeMacros(metabolicKcal, 'lowCarb'),
        highCarb: computeMacros(metabolicKcal, 'highCarb'),
      };
      setMacros(updated);
    }, 50); //  50ms   delay OK
  
    return () => clearTimeout(timeout);
  }, [goal, targetWeight, metabolicKcal]);
  
  

  const metabolicAgePct = Math.min(Number(computed.metabolicAge) / 100, 1);
  const agePct = Math.min(Number(computed.age) / 100, 1);
  const bmrPct = Math.min(Number(computed.bmr) / 2000, 1);
  const bmiPct = Math.min(Number(computed.bmi) / 40, 1);

  const estimatedEnergy = parseFloat(computed.estimatedEnergy || '0');
let customDailyRequiredEnergy = 0;

if (goal === '') {
  customDailyRequiredEnergy = estimatedEnergy * 0.75 - metabolicKcal;
} else if (goal === '') {
  customDailyRequiredEnergy = estimatedEnergy * 1.25 - metabolicKcal;
}

  return (
    <Wrapper>
      <PrintButton onClick={handlePrint}><PrintIcon /></PrintButton>
      <div ref={componentRef}>
        <Container>
          <Header>
            <Title>{computed.name || ' '}  </Title>
            <img src={`${process.env.PUBLIC_URL}/lavida.png`} />
          </Header>

          <Grid>
            <Section>
              <Row><Label></Label><Value><GaugeText>{computed.measureDate || '-'}</GaugeText></Value></Row>
              <Row><Label>(cm)</Label><Value><GaugeText>{computed.height}</GaugeText></Value></Row>
              <Row><Label>(kg)</Label><Value><GaugeText>{computed.weight}</GaugeText></Value></Row>
              <Row><Label>(kg)</Label><Value><GaugeText>{computed.muscleMass}</GaugeText></Value></Row>
              <Row><Label>(kg)</Label><Value><GaugeText>{computed.bodyFatMass}</GaugeText></Value></Row>
              <Row><Label>/(mmHg)</Label><Value><GaugeText>{computed.systolic}/{computed.diastolic}</GaugeText></Value></Row>

              <Row><Label></Label><Value><GaugeFill bg="#a9cce3" percent={metabolicAgePct} /><GaugeText>{computed.metabolicAge}</GaugeText></Value></Row>
              <Row><Label> </Label><Value><GaugeFill bg="#f7c59f" percent={agePct} /><GaugeText>{computed.age}</GaugeText></Value></Row>
              <Row><Label> </Label><Value><GaugeFill bg="#b4e197" percent={bmrPct} /><GaugeText>{computed.bmr}</GaugeText></Value></Row>
              <Row><Label> </Label><Value><GaugeFill bg="#f9e79f" percent={bmiPct} /><GaugeText>{computed.bmi}</GaugeText></Value></Row>
              <Row><Label>  </Label><Value><GaugeText>{computed.estimatedEnergy} kcal/</GaugeText></Value></Row>
              <Row>
  <Label>  </Label>
  <Value>
    <GaugeText>
      {`${Math.round(customDailyRequiredEnergy)} kcal/${
        customDailyRequiredEnergy !== 0
          ? customDailyRequiredEnergy < 0
            ? ' ()'
            : ' ()'
          : ''
      }`}
    </GaugeText>
  </Value>
</Row>

<Row>
  <Label>  </Label>
  <Value>
    <GaugeText>
      {goal === '' ? `${computed.maintenanceCalories} kcal/` : ''}
    </GaugeText>
  </Value>
</Row>


            </Section>

            <Section>
              <SectionTitleRow>(kg)</SectionTitleRow>
              <Row><Label></Label><Value><GaugeText>{computed.idealWeight} kg</GaugeText></Value></Row>
              <Row><Label></Label><Value><GaugeText>{computed.weightControl} kg</GaugeText></Value></Row>
              <Row><Label></Label><Value><GaugeText>{computed.fatControl} kg</GaugeText></Value></Row>
              <Row><Label>  </Label><Value><GaugeText>{computed.muscleControl} kg</GaugeText></Value></Row>
              <Row><Label>  </Label><Value><GaugeText>{computed.dailyProtein} g</GaugeText></Value></Row>
              <Row><Label></Label><Value><GaugeText>{computed.bloodPressureStatus}</GaugeText></Value></Row>
              <SectionTitleRow> (mmHg)</SectionTitleRow>
              <BloodPressureChart systolic={computed.systolicValue} diastolic={computed.diastolicValue} />
            </Section>
          </Grid>

          <SubTitle>WBM  3  </SubTitle>
          <FooterRow>
            <Label> 3   :</Label>
            <FooterContent>
              <KcalText>{metabolicKcal.toFixed(2)} kcal</KcalText>
              <RadioGroup>
                <RadioLabel>
                  <input type="radio" name="goal" value="" checked={goal === ''} onChange={() => setGoal('')} />  
                </RadioLabel>
                <RadioLabel>
                  <input type="radio" name="goal" value="" checked={goal === ''} onChange={() => setGoal('')} />  
                </RadioLabel>
                <RadioLabel>
                  <input type="radio" name="goal" value="" checked={goal === ''} onChange={() => setGoal('')} />  
                </RadioLabel>
              </RadioGroup>
              <FooterInput>
              <PlainInput
  type="number"
  value={targetWeight}
  onChange={(e) => {
    const val = e.target.value;
    if (/^\d*$/.test(val)) setTargetWeight(val); //  
  }}
/>

                kg
              </FooterInput>
            </FooterContent>
          </FooterRow>

<ResultBlock>
  <div style={{ display: 'flex', gap: '24px', justifyContent: 'center' }}>
    {Object.entries(macros).map(([key, value]) => {
      const pieData = [
        { id: '', value: value.carb },
        { id: '', value: value.protein },
        { id: '', value: value.fat },
      ];

      const total = pieData.reduce((sum, item) => sum + item.value, 0);

      return (
        <div key={key} style={{ width: '200px', height: '200px' }}>
          <ResponsivePie
            data={pieData}
            margin={{ top: 0, right: 10, bottom: 40, left: 10 }}
            innerRadius={0.55}
            padAngle={2}
            cornerRadius={2}
            activeOuterRadiusOffset={1}
            colors={{ scheme: 'pastel1' }}
            borderWidth={1}
            borderColor={{ from: 'color', modifiers: [['darker', 0.2]] }}
            arcLabelsSkipAngle={1}
            arcLinkLabelsSkipAngle={10}
            arcLinkLabelsTextColor="#333"
            arcLinkLabelsThickness={2}
            arcLinkLabelsColor={{ from: 'color' }}
            arcLabelsTextColor={{ from: 'color', modifiers: [['darker', 2]] }}
            enableArcLinkLabels={false}
            tooltip={({ datum }) => (
              <div style={{ padding: '6px 9px', background: '#fff', border: '1px solid #ccc' }}>
                <strong>{datum.id}</strong>: {Math.round(datum.value)} g
              </div>
            )}            
            arcLabel={(d) => {
              const percent = Math.round((d.value / total) * 100);
              return `${Math.round(d.value)} g`;
            }}
            legends={[
              {
                anchor: 'bottom',
                direction: 'row',
                justify: false,
                translateY: 24,
                itemWidth: 60,
                itemHeight: 14,
                itemTextColor: '#555',
                symbolSize: 12,
                symbolShape: 'circle',
              },
            ]}
            layers={[
              'arcs',
              'arcLabels',
              'legends',
              //   (value.label)
              (props) => {
                const { centerX, centerY } = props;
                return (
                  <text
                    x={centerX}
                    y={centerY}
                    textAnchor="middle"
                    dominantBaseline="central"
                    style={{
                      fontSize: 14,
                      fontWeight: '700',
                      fill: '#333',
                    }}
                  >
                    {value.label}
                  </text>
                );
              },
              //  kcal 
              (props) => {
                const { centerX, centerY } = props;
            
                const carb = Math.round(value.carb * 4);
                const protein = Math.round(value.protein * 4);
                const fat = Math.round(value.fat * 9);
            
                const kcalItems = [carb, protein, fat];
            
                return (
                  <g transform={`translate(${centerX}, ${centerY + 80})`}>
                    {kcalItems.map((kcal, idx) => (
                      <text
                        key={idx}
                        x={(idx - 1) * 60} // -60, 0, 60 
                        y={40}
                        textAnchor="middle"
                        style={{ fontSize: 12, fill: '#555' }}
                      >
                        {kcal} K
                      </text>
                    ))}
                  </g>
                );
              },
            ]}
          />
        </div>
      );
    })}
  </div>
</ResultBlock>


        </Container>
      </div>
    </Wrapper>
  );
};

export default ReportLayout;
