import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

const DEFAULT_COLOR_ZONES: [string, number, number][] = [
  ['#CC0000', 21, 11],
  ['#FF4500', 18, 9],
  ['#FF8C00', 15, 8],
  ['#FFD700', 13, 7],
  ['#F4A460', 11, 6],
  ['#3CB371', 9, 5],
  ['#B0D9F8', 6, 3],
];

const DialogBackdrop = styled.div`
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
`;

const DialogBox = styled.div`
  background: white;
  padding: 24px;
  border-radius: 8px;
  min-width: 360px;
`;

const ColorRow = styled.div`
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
`;

const ColorInput = styled.input`
  width: 80px;
  height: 32px;
  border: 1px solid #ccc;
`;

const SaveButton = styled.button`
  margin-top: 16px;
  padding: 6px 12px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
`;

interface ColorConfigDialogProps {
  open: boolean;
  onClose: () => void;
}

const ColorConfigDialog: React.FC<ColorConfigDialogProps> = ({ open, onClose }) => {
  const [colors, setColors] = useState<[string, number, number][] | null>(null);

  useEffect(() => {
    try {
      const stored = localStorage.getItem('bloodPressureColors');
      if (stored) {
        setColors(JSON.parse(stored));
      } else {
        setColors(DEFAULT_COLOR_ZONES);
      }
    } catch {
      setColors(DEFAULT_COLOR_ZONES);
    }
  }, []);
  

  const handleChange = (index: number, newColor: string) => {
    if (!colors) return;
    const updated = [...colors];
    updated[index][0] = newColor;
    setColors(updated as [string, number, number][]);
  };

  const handleSave = () => {
    if (!colors) return;
    localStorage.setItem('bloodPressureColors', JSON.stringify(colors));
    onClose();
    window.location.reload(); //    
  };

  const handleBackdropClick = () => {
    onClose(); //    ,     
  };

  if (!open || !colors) return null;

  return (
    <DialogBackdrop onClick={handleBackdropClick}>
      <DialogBox onClick={(e) => e.stopPropagation()}>
        <h3>  </h3>
        {colors.map(([color], i) => (
          <ColorRow key={i}>
            <span>{i + 1}:</span>
            <ColorInput
              type="color"
              value={color}
              onChange={(e) => handleChange(i, e.target.value)}
            />
            <span>{color}</span>
          </ColorRow>
        ))}
        <SaveButton onClick={handleSave}></SaveButton>
      </DialogBox>
    </DialogBackdrop>
  );
};

export default ColorConfigDialog;
